*
{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}
.bg
{
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

  width: 100vw;
  height: 100vh;
}
.todos-container
{
  width: clamp(10rem, 50vw, 20rem);

  position: absolute;
  inset: 1rem 0 0 0;
  margin: 0 auto;
  height: clamp(10rem, 40vh, 20rem);

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  border: 5px dashed rgb(136, 136, 136);
 
}
.todos-container::before
{
  content: '';
  width: inherit;
  height: inherit;
  position: absolute;
  background-color: azure;
  opacity: .2;
  top: -.3rem;
  
}

.todos-container form
{
  position: relative;
  
  width: 80%;
  height: 40%;
  margin-bottom: 2%;

  display: flex;
  flex-direction: column;

}
.todos-container form input[type="text"]
{
  border: .15rem solid rgb(113, 126, 126);
  border-radius: 2rem;
  width: 70%;
  height: clamp(1rem, 5vh, 5rem);
  align-self: center;
  transition: .3s;

}
.todos-container form input[type="text"]::placeholder
{
  text-align: center;
}
.todos-container form input[type="text"]:focus
{
  border-radius: 0;
  outline: none;
  transition: .3s;
}
.todos-container form input[type="text"]:focus ~ input[type="submit"]
{
  opacity: .7;
}

.todos-container input[type="submit"]
{
  align-self: center;
  position: relative;
  top: 1rem;
  cursor: pointer;
  width: clamp(5rem, 10vw, 7rem);
  height: clamp(2rem, 5vh, 3rem);
  border-radius: .5rem;
  border-style: solid;
}
.todos-container input[type="submit"]:hover
{
  opacity: 1 !important;
}
.todos-container ul
{
  position: relative;
  top: 1%;
  display: flex;
  flex-direction: column;
  width: auto;
  overflow-x: hidden;
  justify-content: center;
}
.todos-container ul li
{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 10rem;
  line-height: 1.3rem;
}
.todos-container ul li input
{
  position: relative;
  left: .25rem;
  justify-self: flex-end;
  cursor: pointer;
  
}
.todos-container button
{
  margin-top: 1rem;
  cursor: pointer;
  z-index: 1;
}